@import "../utils.scss";

.main {
  width: 100vw;
  height: 100%;
  overflow-x: hidden;
  .main__wrap {
    @include mobile {
      padding: 20vw 0;
    }
    padding: 5% 0;
    .search {
      display: grid;
      place-items: center;
      width: 100%;
      height: 10vmax;
      div {
        position: relative;
        display: flex;
        align-items: center;
        width: 30%;
        height: 30%;
        @include mobile {
          width: 50%;
        }
        .search__form {
          width: 100%;
          height: 100%;
          padding: 0 5%;
          border-bottom: 1px solid black;
          input {
            width: 90%;
            height: 95%;
            font-size: 2vmax;
            overflow: hidden;
            border: none;
            background: none;
            outline: none;
          }
          button {
            cursor: pointer;
            border: none;
            background: transparent;
            position: absolute;
            height: 100%;
            width: 3vmax;
            svg {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }

    @mixin cards {
      width: 90%;
      height: 100%;
      display: flex;
      flex-direction: column;
      font-size: 1vmax;
      padding-bottom: 10%;
      margin-top: 1vmax;
    }
    @mixin card {
      position: relative;
      display: grid;
      border-top: 1px solid black;
      height: 100%;
      grid-template-columns: repeat(4, 25%);
      grid-template-rows: 15vmax;
      padding-top: 1vmax;
      @include mobile {
        grid-template-columns: 100%;
        grid-template-rows: repeat(1, 4vmax 15vmax 15vmax 5vmax);
        padding-top: initial;
      }
      .name {
        width: 90%;
        margin: 0 auto;
        @include mobile {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin: auto;
        }
        h3 {
          text-transform: capitalize;
          line-height: 1.3;
          font-size: 1.2vmax;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .button {
          cursor: pointer;
          margin-top: 1.2vmax;
          font-size: 1.1vmax;
          border-radius: 50px;
          border: 1px solid black;
          background-color: #6dc2f4;
          width: fit-content;
          line-height: 1.5;
          padding: 0 2%;
          &.delete {
            background-color: #f46d6d;
          }
          @include mobile {
            margin-top: initial;
          }
        }
      }
      .img {
        width: 90%;
        height: 90%;
        background-size: cover;
        background-position: center top;
        background-repeat: no-repeat;
        @include mobile {
          width: 100%;
          background-size: contain;
        }
      }
      .detail {
        margin: 0 auto;
        width: 90%;
        height: 100%;
        overflow: scroll;
        &::-webkit-scrollbar {
          display: none;
        }
        p {
          font-size: 1.1vmax;
        }
        ul {
          list-style: auto;
          margin: auto;
          margin-top: 1.2vmax;
          width: 90%;
          height: 100%;
          font-size: 0.9vmax;
          font-weight: 300;
          li {
            line-height: 1.5;
          }
        }
      }
      .health {
        margin: auto;
        width: 90%;
        height: 100%;
        @include mobile {
          overflow: scroll;
          &::-webkit-scrollbar {
            display: none;
          }
        }
        ul {
          display: flex;
          align-items: center;
          align-content: center;
          flex-wrap: wrap;
          list-style: none;
          li {
            font-size: 0.9vmax;
            border-radius: 50px;
            line-height: 1.5;
            margin: 1.5% 3%;
            padding: 0 3%;
            @include mobile {
              margin: 1% 0.5%;
            }
          }
        }
      }
    }

    .cards__wrap {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 100%;
      h1 {
        width: 90%;
        font-family: "Open Sans", sans-serif;
        font-weight: 500;
        font-size: 2vmax;
        .refetch {
          cursor: pointer;
          font-size: 1.5vmax;
          margin-left: 1vmax;
          position: relative;
          &::after {
            width: 130%;
            border: 1px solid black;
            height: 100%;
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }

      .cards {
        @include cards;
        .card {
          @include card;
        }
      }
    }
  }
}
