@import "../utils.scss";

nav {
  @include tablet {
    height: 10vw;
  }
  position: fixed;
  width: 100vw;
  height: 4vw;
  z-index: 300;
  .link {
    top: 10%;
    left: 2%;
    position: absolute;
    height: 150%;
    width: 10%;
    div {
      margin-left: 10%;
      width: 100%;
      height: 100%;
      background: url("./images/logo.png") no-repeat;
      background-size: contain;
    }
  }
  .buttons {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 50%;
    height: 100%;
    position: absolute;
    right: 2%;
    > div {
      margin: 0 5%;
      font-weight: 300;
      text-transform: capitalize;
      cursor: pointer;
      font-size: 1.5vmax;
      line-height: 1.5;
      border-radius: 50px;
      border: 1px solid black;
      padding: 0 3%;
      background-color: #fff;
    }
  }
}
